
/************ Responsive CSS for Mobile ************/
@media (max-width: 768px) {

  nav.navbar {
    padding: 10px 20px;
  }

  nav.navbar a.navbar-brand {
    width: 30%;
  }

  .navbar-text button {
    padding: 12px 20px;
    font-size: 14px;
  }

  .banner {
    padding: 150px 20px 60px 20px;
    text-align: center;
  }

  .banner h1 {
    font-size: 36px;
  }

  .banner p {
    font-size: 16px;
    width: 100%;
  }

  .banner button {
    margin: 30px auto 0;
    font-size: 16px;
  }

  .skill-bx {
    padding: 30px 20px;
    border-radius: 30px;
  }

  .skill h2, .project h2 {
    font-size: 32px;
  }

  .skill p, .project p {
    font-size: 16px;
    width: 100%;
  }

  .project .nav.nav-pills {
    width: 100%;
    flex-direction: column;
  }

  .project .nav.nav-pills .nav-item {
    width: 100%;
  }

  .proj-txtx h4 {
    font-size: 20px;
  }

  .proj-txtx span {
    font-size: 14px;
  }

  .skill-slider {
    width: 100%;
  }

  .skill-card {
    height: auto;
    padding: 20px;
    margin: 10px 5px;
  }

  .work-exp-card {
    padding-left: 15px;
  }

  .timeline-dot {
    width: 12px;
    height: 12px;
    left: -6px;
  }

  .expertise {
    padding: 30px 15px;
  }

  .skills-grid {
    grid-template-columns: 1fr; /* Stack categories vertically */
  }
}
